.Scrollup {
  display: none;
  position: fixed;
  bottom: 20px;
  left: 95vw;
  width: 50px;
  height: 50px;
  border: 0;
  border-radius: 50%;
  background-image: url('../../images/arrowUp.svg');
  background-size: 50%;
  background-repeat: no-repeat;
  background-position: center;

  cursor: pointer;
  z-index: 99;
  transition: transform 0.5s, background-color 0.5s;
}
.Scrollup:hover {
  transform: scale(1.1);
}
